main {
    width: 100%;
}

.homeTop {
    width: 100%;
    background-color: #f9f9fb;
    background-image: url(../../images/top_background.png);
    background-position: right;
    background-repeat: no-repeat;
    background-size: contain;

    @media (max-width: 1600px) {
        background-image: url(../../images/top_backgroundMobile.png);
        background-position-y: 0;
    }

    &__about {
        text-align: start;
        color: #565756;
        font-family: "Montserrat-Black";
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        padding-top: 105px;
        padding-left: 267px;
        letter-spacing: 1px;
        font-size: 66px;
        padding-bottom: 64px;

        @media (max-width: 1600px) {
            display: none;
        }
    }

    &__mobileAbout {
        text-align: start;
        color: #565756;
        font-family: "Montserrat-Black";
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        padding-top: 0;
        letter-spacing: 2px;
        font-size: 87px;
        padding-bottom: 50px;

        @media (min-width: 1600px) {
            display: none;
        }

        @media (max-width: 900px) {
            font-size: 44px;
            padding-top: 20px;
            padding-bottom: 20px;
        }

        @media (max-width: 600px) {
            font-size: 32px;
        }

        @media (max-width: 400px) {
            font-size: 26px;
        }
    }

    &__aside {
        background-color: rgba(255, 255, 255, 0.9);
        padding-top: 91px;
        width: 1552px;
        height: 1010px;
        border-radius: 30px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        padding-left: 266px;

        @media (max-width: 1600px) {
            width: auto;
            height: -webkit-max-content;
            height: -moz-max-content;
            height: max-content;
            padding-left: 0;
            margin: 0 auto;
            width: calc(100% - 100px);
            margin-top: 700px;
            padding-left: 41px;
            padding-top: 67px;
        }

        @media (max-width: 900px) {
            padding-top: 0;
            margin-top: 600px;
        }

        @media (max-width: 600px) {
            width: calc(100% - 57px);
            margin-top: 467px;
        }

        @media (max-width: 400px) {
            width: calc(100% - 20px);
            padding: 10px;
            margin-top: 288px;
        }
    }

    &__asideHeader {
        color: #565756;
        text-align: start;
        font-size: 43px;
        line-height: 158%;
        padding-bottom: 59px;

        @media (max-width: 1600px) {
            font-size: 40px;
            line-height: 143%;
            max-width: 90%;
        }

        @media (max-width: 900px) {
            font-size: 24px;
        }

        @media (max-width: 600px) {
            font-size: 19px;
            padding-bottom: 29px;
        }

        @media (max-width: 400px) {
            font-size: 16px;
        }
    }

    br {
        @media (max-width: 1600px) {
            display: none;
        }
    }

    &__text {
        letter-spacing: 0.3px;
        font-size: 37px;
        line-height: 165%;
        color: #565756;
        max-width: 1200px;
        text-align: start;
        padding-bottom: 57px;

        @media (max-width: 1600px) {
            max-width: 93%;
            line-height: 152%;
            padding-bottom: 52px;
            letter-spacing: 1px;
        }

        @media (max-width: 900px) {
            font-size: 21px;
        }

        @media (max-width: 600px) {
            font-size: 17px;
        }

        @media (max-width: 400px) {
            font-size: 14px;
            padding-bottom: 38px;
        }
    }
}

.breedcrumps {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-left: 266px;
    padding-top: 47px;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;

    @media (max-width: 1600px) {
        padding-left: 44px;
    }

    @media (max-width: 600px) {
        padding-top: 22px;
    }

    &__home {
        margin-right: 0;
        height: 27px;

        @media (max-width: 1600px) {
            height: 36px;
        }

        @media (max-width: 900px) {
            height: 23px;
        }

        @media (max-width: 900px) {
            height: 19px;
        }
    }

    &__textMain {
        color: #565756;
        font-size: 23px;
        padding-left: 17px;
        letter-spacing: 1px;

        @media (max-width: 1600px) {
            font-size: 32px;
        }

        @media (max-width: 900px) {
            font-size: 23px;
        }

        @media (max-width: 600px) {
            font-size: 18px;
        }
    }

    &__text {
        color: #8c8c93;
        font-size: 23px;
        letter-spacing: 1px;

        @media (max-width: 1600px) {
            font-size: 32px;
        }

        @media (max-width: 900px) {
            font-size: 23px;
        }

        @media (max-width: 600px) {
            font-size: 18px;
        }
    }

    &__next {
        padding-left: 22px;
        padding-right: 26px;
        height: 12px;
        padding-top: 5px;

        @media (max-width: 1600px) {
            padding-left: 31px;
            padding-right: 32px;
            height: 18px;
        }

        @media (max-width: 900px) {
            height: 15px;
            padding: 0 10px;
        }

        @media (max-width: 900px) {
            height: 12px;
        }
    }
}

.homeMiddle {
    width: 100%;
    background-color: #f8f8fa;

    &__header {
        text-align: start;
        color: #565756;
        font-family: "Montserrat-Black";
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        padding-top: 105px;
        padding-left: 267px;
        letter-spacing: 1px;
        font-size: 66px;
        padding-bottom: 64px;
        padding-top: 127px;

        @media (max-width: 1600px) {
            padding-left: 89px;
            padding-top: 78px;
            font-size: 91px;
            line-height: 119%;
            letter-spacing: 0px;
            padding-bottom: 52px;
        }

        @media (max-width: 900px) {
            font-size: 44px;
            padding-top: 20px;
            padding-bottom: 20px;
        }

        @media (max-width: 600px) {
            font-size: 32px;
            padding-left: 71px;
        }

        @media (max-width: 400px) {
            font-size: 26px;
            padding-left: 23px;
            padding-right: 23px;
        }
    }

    &__text {
        padding-left: 267px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        padding-top: 43px;
        max-width: 2407px;
        letter-spacing: 0.3px;
        font-size: 37px;
        line-height: 165%;

        @media (max-width: 1600px) {
            line-height: 152%;
            padding-top: 0;
            padding-left: 93px;
            letter-spacing: 1px;
            width: 90%;
        }

        @media (max-width: 900px) {
            font-size: 23px;
        }

        @media (max-width: 900px) {
            font-size: 17px;
            padding-left: 71px;
        }

        @media (max-width: 400px) {
            font-size: 14px;
            padding-left: 23px;
        }
    }
}

.homeCardsWrapper {
    width: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    padding-top: 159px;
    padding-left: 359px;

    @media (max-width: 1600px) {
        padding-top: 33px;
        padding-left: 183px;
    }

    @media (max-width: 900px) {
        width: 100%;
        justify-content: center;
        padding-left: 0;
    }

    &__card {
        margin-right: 249px;
        width: 484px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        margin-bottom: 98px;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-transition: -webkit-transform 1s;
        transition: -webkit-transform 1s;
        -o-transition: transform 1s;
        transition: transform 1s;
        transition: transform 1s, -webkit-transform 1s;

        @media (max-width: 1600px) {
            width: 683px;
            margin-right: 173px;
        }

        @media (max-width: 900px) {
            width: 440px;
            margin: 0;
        }

        @media (max-width: 600px) {
            width: 95%;
        }
    }

    &__card:hover {
        cursor: pointer;
        -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
        transform: scale(1.2);

        @media (max-width: 900px) {
            transform: scale(1.1);
        }
        
        @media (max-width: 600px) {
            transform: scale(1.05);
        }
    }

    &__avatar {
        height: 484px;

        @media (max-width: 1600px) {
            height: 683px;
        }

        @media (max-width: 900px) {
            height: 440px;
        }

        @media (max-width: 600px) {
            height: 380px;
        }

        @media (max-width: 400px) {
            height: 278px;
        }
    }

    &__name {
        color: #424348;
        font-family: "Montserrat-Black";
        font-size: 42px;
        padding-top: 86px;

        @media (max-width: 1600px) {
            font-size: 54px;
            padding-top: 101px;
            font-family: "Montserrat-bold";
        }

        @media (max-width: 900px) {
            font-size: 39px;
            padding-top: 36px;
        }

        @media (max-width: 600px) {
            font-size: 33px;
        }

        @media (max-width: 400px) {
            font-size: 28px;
        }
    }

    &__specialization {
        color: #c4c4cd;
        font-size: 32px;
        max-width: 484px;
        padding-top: 33px;
        line-height: 150%;

        @media (max-width: 1600px) {
            max-width: 683px;
            font-size: 36px;
            padding-top: 46px;
            padding-bottom: 19px;
        }

        @media (max-width: 900px) {
            padding-top: 0px;
            padding-bottom: 62px;
        }

        @media (max-width: 600px) {
            font-size: 26px;
        }

        @media (max-width: 400px) {
            font-size: 23px;
        }
    }
}

.otherPagesWrapper {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding-bottom: 100px;
    padding-right: 359px;

    @media (max-width: 1600px) {
        padding-right: 183px;
    }

    @media (max-width: 900px) {
        padding-right: 0px;
        width: 100%;
    }

    &__active,
    &__inactive {
        background-color: transparent;
        border: none;
        outline: none;
        color: #95c124;
        font-size: 72px;
        -webkit-transition: color 1s;
        -o-transition: color 1s;
        transition: color 1s;
        padding: 0 10px;

        @media (max-width: 900px) {
            font-size: 44px;
        }

        @media (max-width: 600px) {
            font-size: 39px;
        }
    }

    &__active:hover {
        cursor: pointer;
        color: #424348;
    }

    &__inactive {
        color: #565756;
    }
}
