@font-face {
  font-family: "Montserrat-Medium";
  src: url(./fonts/Montserrat-Medium.otf), url(./fonts/Montserrat-Medium.ttf);
}

@font-face {
  font-family: "Montserrat-bold";
  src: url(./fonts/Montserrat-Bold.otf), url(./fonts/Montserrat-Bold.ttf);
}

@font-face {
  font-family: "Montserrat-Black";
  src: url(./fonts/Montserrat-Black.otf), url(./fonts/Montserrat-Black.ttf);
}

.App {
  text-align: center;
  width: 100%;
  font-family: "Montserrat-Medium";
}

a {
  text-decoration: none;
}

button {
  outline: none;
}

p,
h1,
h2 {
  margin: 0;
}

ul {
  list-style: none;
}

li {
  margin: 0;
  padding: 0;
}
